.copybutton {
    border-radius: 4px;
    background: linear-gradient(to right, #67b26b, #4ca2cb) !important;
    border: none;
    color: #FFFFFF;
    text-align: center;
    text-transform: uppercase;
    font-size: 22px;
    padding: 20px;
    width: 200px;
    transition: all 0.4s;
    cursor: pointer;
    margin: 20px 5px 0px 0px;
}
.copybutton span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.4s;
}
.copybutton span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -40px;
    transition: 0.5s;
}
.copybutton:hover span {
    padding-right: 25px;
}
.copybutton:hover span:after {
    opacity: 1;
    right: 0;
}

.error {
    color: red
}

.warning {
    color: orange
}

